.sidebar {
  height: 100%;
  display: flex;
  overflow-y: auto;
  width: 100%;
  max-width: 400px;
  background-color: #465945;
height: calc(100vh - 46px);
}
.sidebar-wrapper {
  max-width: 400px;
  width: 100%;
  display: flex;
  flex-direction: column;
  //   flex: 1;

}

.sidebar-wrapper-navlink {
  width: 100%;
  background: #465945;
  padding: 12px 12px 12px 32px;
  color: white;
  cursor: pointer;
  text-decoration: none;
}

.sidebar-wrapper-navlink.active {
  border-right: 4px solid #ffa500;
}
